<script>
	import { _, locale } from '../services/i18n';
    let menuShow  = false;
  
    function toggleNavbar() {
        menuShow = !menuShow;
    }

    let dropdownPopoverShow = false;

    const toggleDropdown = () => {
        dropdownPopoverShow = !dropdownPopoverShow;
    };

    const handleDropdown = (event) => {
      isOpen = event.detail.isOpen;
    };

</script>
  
<nav class="top-0 relative z-50 w-full  items-center justify-between px-2 py-3 shadow-lg">
  <div class="container px-4 mx-auto flex flex-wrap items-center justify-between">
    <div class="w-full relative flex justify-between lg:w-auto lg:static lg:block lg:justify-start ">
      <div class="text-sm font-bold leading-relaxed inline-block mr-4 py-2 whitespace-nowrap uppercase text-white">JH <small class="px-2">{$_('home.subtopic', { default: "~" })}</small></div>
      <button class="cursor-pointer text-xl leading-none px-3 py-1 border border-solid border-transparent rounded bg-transparent block lg:hidden outline-none focus:outline-none" type="button" on:click={() => toggleNavbar()}>
        <i class="text-white fas fa-bars"></i>
      </button>
    </div>
    <div class="lg:flex lg:flex-grow items-center text-white {menuShow ? 'flex':'hidden'}">
        <ul class="flex flex-col lg:flex-row list-none lg:ml-auto">
            <li class="nav-item">
                <a href="#contact" class="hover:opacity-75 px-3 py-2 lg:py-2 flex items-center text-xs uppercase font-bold">
                    {$_('contact.topic')}
                </a>
            </li>
            <li class="nav-item">
                <a href="{$_('contact.linkedin')}" target="_blank" class="hover:opacity-75 px-3 py-2 lg:py-2 flex items-center text-xs uppercase font-bold">
                Linkedin</a>
            </li>
            <li class="nav-item">
                <a href="{$_('contact.github')}" target="_blank" class="hover:opacity-75 px-3 py-2 lg:py-2 flex items-center text-xs uppercase font-bold">
                Github
                </a>
            </li>
            <li class="nav-item">
                <button on:click="{toggleDropdown}" target="_blank" class="hover:opacity-75 px-3 pb-2 lg:pb-2 flex items-center text-lg uppercase">
                💬
                </button>
            </li>
            <div class="relative ml-1 text-base z-50 float-left py-2 list-none text-left rounded shadow-lg lg:absolute lg:ml-10 lg:mt-10 w-40  {dropdownPopoverShow ? 'visible':'hidden'}" on:update={handleDropdown}>
                <a href="#english" class="text-sm py-2 px-4 font-normal block w-full whitespace-no-wrap bg-transparent text-white" on:click={() => { locale.set("en"); toggleDropdown()}}>
                    English
                </a>
                <a href="#español" class="text-sm py-2 px-4 font-normal block w-full whitespace-no-wrap bg-transparent text-white" on:click={() => { locale.set("es"); toggleDropdown()}}>
                    Español
                </a>
                <a href="#nihongo" class="text-sm py-2 px-4 font-normal block w-full whitespace-no-wrap bg-transparent text-white" on:click={() => { locale.set("jp"); toggleDropdown()}}>
                    日本語
                </a>
              </div>
        </ul>
        </div>
    </div>
</nav>
